import BaseCWTrainer from './BaseCWTrainer';
import { NUMBERS, LETTERS, SYMBOLS, DISPLAY_MODES } from './constants';
import { CwTrainer } from './cwTrainer';


export default class RandonCharsCWTrainer extends BaseCWTrainer {


    // eslint-disable-next-line no-useless-constructor
    constructor(wpm, farnsworthWpm, useLetters, useNumbers, useSymbols, totalCharRepeats, displayMode, onDisplay) {
        super(wpm, farnsworthWpm, useLetters, useNumbers, useSymbols, totalCharRepeats, displayMode, onDisplay);
    }

    _getTextToSend() {
        
        let baseText = '';
        let text = '';

        if (this.useLetters) {
            for (let i = 0; i < LETTERS.length; i++) {
                baseText += LETTERS[i];    
            }
        }

        if (this.useNumbers) {                
            for (let i = 0; i < NUMBERS.length; i++) {
                baseText += NUMBERS[i];    
            }
        }

        if(this.useSymbols) {
            for (let i = 0; i < SYMBOLS.length; i++) {
                baseText += SYMBOLS[i];    
            }
        }

        const min = 0;
        const max = baseText.length -1;

        for(let i = 0; i < 50; i++) {            
            const randomIndex = Math.floor(Math.random() * (max - min + 1)) + min;
            text += baseText[randomIndex] ;   
        }


        return text;

    }

    setDisplayMode(displayMode) {

        this.resetCwTrainerCallbacks();            

        switch (displayMode) {
            case DISPLAY_MODES.IMMEDIATE.id: {
                CwTrainer.getInstance().setBeforeCharCallback((character) => {this.onDisplay(character)});
                CwTrainer.getInstance().setAfterCharCallback((character) => {this.onDisplay('')});                                
                break;
            }
            case DISPLAY_MODES.LINGERING.id: {
                CwTrainer.getInstance().setBeforeCharCallback((character) => {this.onDisplay(character)});
                break;
            }
            case DISPLAY_MODES.DELAYED.id: {
                CwTrainer.getInstance().setBeforeCharCallback((character) => {this.onDisplay('')});
                CwTrainer.getInstance().setAfterCharCallback((character) => {this.onDisplay(character)});
                break;
            }
            default: {
                break;
            }
        }
    }

};