import BaseCWTrainer from './BaseCWTrainer';
import { NUMBERS, LETTERS, SYMBOLS, DISPLAY_MODES } from './constants';
import { CwTrainer } from './cwTrainer';


const GROUP_SIZE = 5;

export default class RandonGroupsCWTrainer extends BaseCWTrainer {


    // eslint-disable-next-line no-useless-constructor
    constructor(wpm, farnsworthWpm, useLetters, useNumbers, useSymbols, totalCharRepeats, displayMode, onDisplay) {
        super(wpm, wpm, useLetters, useNumbers, useSymbols, totalCharRepeats, displayMode, onDisplay);
        this.charSentCounter = 0;
        this.numberOfWhiteSpaces = farnsworthWpm;
        this.whiteSpacesLength = 0;
    }

    _getWhitespaces() {

        let ret = '';
        for(let i = 0; i < this.numberOfWhiteSpaces; i++) {
            ret += ' ';
        }
        this.whiteSpacesLength = ret.length;
        return ret;
    }

    _getTextToSend() {
        
        let baseText = '';
        let text = '';

        if (this.useLetters) {
            for (let i = 0; i < LETTERS.length; i++) {
                baseText += LETTERS[i];    
            }
        }

        if (this.useNumbers) {                
            for (let i = 0; i < NUMBERS.length; i++) {
                baseText += NUMBERS[i];    
            }
        }

        if(this.useSymbols) {
            for (let i = 0; i < SYMBOLS.length; i++) {
                baseText += SYMBOLS[i];    
            }
        }

        const min = 0;
        const max = baseText.length -1;

        for(let i = 0; i < GROUP_SIZE; i++) {            
            const randomIndex = Math.floor(Math.random() * (max - min + 1)) + min;
            text += baseText[randomIndex] ;                       
        }

        text += this._getWhitespaces();
        this.textToSend = text;

        return text;

    }

    setDisplayMode(displayMode) {

        this.resetCwTrainerCallbacks();            

        switch (displayMode) {
            case DISPLAY_MODES.IMMEDIATE.id: {

                CwTrainer.getInstance().setBeforeCharCallback((character) => {

                    if(this.charSentCounter === 0) {
                        this.onDisplay(this.textToSend);        
                    }
                    else if (this.charSentCounter === (this.textToSend.length - this.whiteSpacesLength)) {
                        this.onDisplay('');
                    }
                });  
                
                CwTrainer.getInstance().setAfterCharCallback((character) => {

                    this.charSentCounter++;
                    if (this.charSentCounter === (this.textToSend.length - this.whiteSpacesLength)) {
                        this.onDisplay('');
                        this.charSentCounter = 0;
                    }
                });
                
                break;
            }
            case DISPLAY_MODES.LINGERING.id: {

                CwTrainer.getInstance().setBeforeCharCallback((character) => {
                    if(this.charSentCounter === 0) {
                        this.onDisplay(this.textToSend);         
                    }
                }); 

                CwTrainer.getInstance().setAfterCharCallback((character) => {

                    this.charSentCounter++;
                    if(this.charSentCounter === 0) {
                        this.onDisplay('');        
                    }
                    else if (this.charSentCounter === (this.textToSend.length - this.whiteSpacesLength)) {
                        this.onDisplay(this.textToSend);
                        this.charSentCounter = 0;
                    }
                });  
                break;
            }
            case DISPLAY_MODES.DELAYED.id: {

                CwTrainer.getInstance().setBeforeCharCallback((character) => {
                    if(this.charSentCounter === 0) {
                        this.onDisplay('');         
                    }
                }); 

                CwTrainer.getInstance().setAfterCharCallback((character) => {

                    this.charSentCounter++;
                    if(this.charSentCounter === 0) {
                        this.onDisplay('');        
                    }
                    else if (this.charSentCounter === (this.textToSend.length - this.whiteSpacesLength)) {
                        this.onDisplay(this.textToSend);
                        this.charSentCounter = 0;
                    }
                });  
                                
                break;
            }
            default: {
                break;
            }
        }
    }

};